import { AbstractControl } from '@angular/forms';
import { FORM_BUILDER } from '../form-builder.constant';
import { AbstractFormControlModel } from './abstract-form-control.model';
import { IFormControlOptions } from './interfaces/form-control-options.interface';

export class FormControlModel extends AbstractFormControlModel {
  type = FORM_BUILDER.controlTypes.formControl;
  component: any;
  name: string;
  isDisabled = false;
  formControl: AbstractControl;
  options: any;

  constructor(options: IFormControlOptions, formControl: AbstractControl) {
    super();

    this.formControl = formControl;
    this.name = options.name;
    this.component = options.component;
    this.hidden = options.hidden;
    this.isDisabled = options.isDisabled;
    this.options = options.options || {};
  }
}
